.header{
    box-shadow: 0px 3px 6px #0000000A;
    opacity: 1;
    margin: 0;
    padding: 0;
    display: flex;
}

.header-img{
    margin-top: 31px;
    margin-left: 45px;
    width: 170px;
    height: 28.53px;
    background-color: crimson;

}

.header-search{
    padding: 0;
    margin-top: 23px;
    margin-left: 105px;
    margin-bottom: 22px;
    padding-left: 29.72px;
    height: 45px;
    width: 500px;
    outline: none;
    border: 0px;
