.main{
    position: relative;
    background: #F3F3F3;
    width: 100%;
    height: 100%;

}

.leads{
    margin-left: 30px;
    margin-top: 30px;
    width: 1578px;

    border-radius: 10px;
    background-color: white;
}

.leads-top{
    display: flex;
}

.leads-top div:first-child{
    font-family: 'Museo Sans Cyrl 700', sans-serif;
    font-size: 28px;
    color: #43425D;
    padding-top: 39px;
    margin-left: 30px;
}

.leads-top-search {
    margin-left: 60px;
    margin-top: 30px;
    width: 1168px;
    height: 50px;
    border: 1px solid #DCDDE8;
    border-radius: 27px;
}

.leads-top-search input[type="search"] {
    border: none;
    outline: none;
    -webkit-appearance: none;

    margin-top: 16px;
    margin-left: 24px;
    width: 975px;
}

.leads-top-search input[type="submit"] {
    padding: 0;
    width: 20px;
    height: 20px;
    border: none;
    vertical-align: middle;
    background-image: url("../../img/Magnifier_icon-icons.com_52024.png"); /* Параметры фона */
}

.leads-top-create{
    width: 156px;
    height: 50px;
    margin-left: 60px;
    margin-top: 30px;
    font-family: 'Museo Sans Cyrl 700', sans-serif;
    font-size: 15px;
    background: linear-gradient(to right, #79B7EE, #67E6BD);
    border: 0;
    border-radius: 30px;
    text-align: center;
    letter-spacing: 0px;
    color: #FFFFFF;
}

.line{
    background-color: #F3F3F3;
    width: 100%;
    height: 1px;
    margin-top: 28px;

}

.form_radio_btn {
    display: inline-block;
    margin-right: 10px;
    margin-top: 30px;
    margin-bottom: 30px;
}

.form_radio_btn:first-child{
    margin-left: 30px;
}

.radio-select{
    font-family: 'Museo Sans Cyrl 500', sans-serif;
    font-size: 15px;
    color: #ADB3D0;
    padding-left: 15px;
    padding-right: 57px;
    outline: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    border: 1px solid #E8E9EC;
    border-radius: 17px;
}

.form_radio_btn input[type=radio] {
    display: none;
}
.form_radio_btn label {
    font-family: 'Museo Sans Cyrl 500', sans-serif;
    font-size: 15px;
    display: inline-block;
    cursor: pointer;
    padding: 0px 15px;
    color: #ADB3D0;
    border: 1px solid #E8E9EC;
    border-radius: 17px;
    user-select: none;
}

.form_radio_btn input[type=radio]:checked + select {
    background: #EFF2FB;
    color: #4D4F5C;
}

.form_radio_btn input[type=radio]:checked + label {
    background: #EFF2FB;
    color: #4D4F5C;
}

.form_radio_btn label:hover {
    color: #4D4F5C;
    border-color: #ADB3D0;

}

.list{
    margin-left: 30px;
    margin-top: 30px;
    width: 1578px;
    height: 638px;
    border-radius: 10px;
    background-color: white;
}

table{
    font-family: 'Museo Sans Cyrl 500', sans-serif;
    font-size: 15px;
}

thead{
    padding-top: 30px;
    font-family: 'Museo Sans Cyrl 700', sans-serif;
    font-size: 15px;
    color: #43425D;
}

td:first-child{
    margin-left: 30px;
}

.content {
    width: 1578px;
    height: 638px;
    background: #fff;
    margin: 30px 0 30px 30px;
    border-radius: 10px;
}
.content__head {
    display: flex;
    padding: 30px 0 30px 30px;
    color: #000000;
    font-size: 15px;
    font-family: "Museo Sans Cyrl 700";
    border-bottom: 1px solid #f3f3f3;
}
.content__head--id {
    margin-right: 72px;
}
.content__head--leads {
    margin-right: 295px;
}
.content__head--contacts {
    margin-right: 183px;
}
.content__head--source {
    margin-right: 133px;
}
.content__head--cause {
    margin-right: 289px;
}
.content__head--category {
}

.content__users__items {
    display: flex;
    width: 100%;
    height: 49px;
    border-bottom: 1px solid #f3f3f3;
    padding: 0 0 0 30px;
    margin-top: 30px;
    color: #43425d;
