.content__users__item--2 {
    width: 335px;
}
.content__users__item--3 {
    width: 253px;
}
.content__users__item--4 {
    width: 290px;
}
.content__users__item--5 {
    width: 439px;
}
.content__users__item--6 {
    height: 19px;
    color: #fff;
